import React from "react";

export default function Home() {
  const [displayList] = React.useState([
    { id: 1, value: "Wai Zaw Tun." },
    { id: 2, value: "Frontend Developer." },
    { id: 3, value: "Junior Developer." },
  ]);

  const [displayName, setDisplayName] = React.useState(displayList[0].value);
  const [index, setIndex] = React.useState(1);

  React.useEffect(() => {
    setInterval(() => {
      setDisplayName(displayList[index].value);
      if (index < displayList.length - 1) {
        setIndex(index + 1);
      } else {
        setIndex(0);
      }
    }, 5000);
  }, [displayList, index]);

  return (
    <div className="home">
      <div className="intro">
        <h2 className="iam">I am </h2>
        <h2 className="name" id="name">
          {/* {displayList[0].value} */}
          {displayName}
        </h2>
      </div>
    </div>
  );
}
